@font-face {
  font-family: 'KakaoBold';
  src: url('./webfont/KakaoBold.woff2') format('woff2'),
      url('./webfont/KakaoBold.woff') format('woff');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'KakaoRegular';
  src: url('./webfont/KakaoRegular.woff2') format('woff2'),
      url('./webfont/KakaoRegular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

*{margin:0; padding:0; font-family: 'kakaoRegular';  font-size:10px;}
a{color:inherit; text-decoration:none; font-size:1.9rem;}
h1,h2{font-size:2.5rem;}
p{letter-spacing: 150%; font-size:2.1rem;}
i{cursor:pointer; font-size:2rem;}
li{list-style: none;}
span{font-size:2.2rem}
html{
  background:RGB(255, 211, 109);
  scroll-behavior: smooth;
}
.wrap{
  width:100%;
  max-width: 1280px;
  margin:auto;
}
header{
  z-index: 9999;  
  position:fixed;
  margin: 0 auto;
  left: 0;
  right: 0;
  width:100%;
  max-width: 1280px;
  height:13vh;
  display:flex;
  justify-content: space-between;
  align-items:center;
}
.logo{
  display:block; 
  width:8%;
  height:50px;
} 
#logo{
  fill-opacity:1
}

header i{
  font-size:3rem;
} 
.user{
  margin-left:20px;
  width:100%;
  height:150px;
  display:flex;
  align-items: center;
}
hr{
  width:94%;
  margin:auto;
  background:#c1c1c1c7;
  height:1px;
  border:0;
}
.user-img{
  width:80px;
  height:80px;
  border-radius:20px;
  background:url(./img/profile_default.png)no-repeat center;
  background-size:100%;
  margin-right:20px;
}
.user-name{
  font-weight: bold;
  font-size:2.2rem;
  display:flex;
  align-items: center;
}
.user-name i{
  color:#818181;
  font-size:1.5rem;
}
.menu{
  width:25vw;
  display:flex;
}
.menu li a{
  font-size:2.3rem;
  margin-left:20px;
}
.menu li a:hover{
  font-weight: bold;
}
.show-menu{
  z-index: 9999;
  position: fixed;
  top:48px;
  right: 300px;
  transition: 1s;
}

.hide-menu{
  opacity: 0;
  top:48px;
  position:fixed;
  right:0;
  transition:1s;
}

/*----------main---------*/
.main{
  width:100%;
  margin:auto 0;
  height:100vh;
  display:flex;
  align-items: center;
  justify-content: space-between;
}
.cont-wrap{
  width:100%;
}
@keyframes fadeInUp{
  0%{
      opacity:0;
      transform:translate3d(-10px, 50px ,0)
  }
  to{
      opacity:1;
      transform:translate3d(0, 0 ,0)
  }
}
@keyframes fadeInUp2{
  0%{
      opacity:0;
      transform:translate3d(15px, 50px ,0)
  }
  to{
      opacity:1;
      transform:translateZ(0)
  }
}

.cont1{
  margin-top:70px;
  width:55%;
  height:16vh;
  background:url(img/SVG/cont1.svg)no-repeat;
  background-size:contain;
  animation: fadeInUp 1s;
}

.cont2{
  margin-left:10px;
  margin-top:30px;
  width:55%;
  height:16vh;
  background:url(img/SVG/cont2.svg)no-repeat;
  background-size:contain;
  animation: fadeInUp 1s;
  animation-delay: 0.3s;
}
.cont3{
  margin-top:50px;
  float:right;
  width:150px;
  height:16vh;
  background:url(img/SVG/cont3.svg)no-repeat;
  background-size:contain;
  animation: fadeInUp2 1s;
  animation-delay: 0.6s;
}

/*-------info--------*/
.info{
  height:100vh;
  display:flex;
  width:100%;
  justify-content: space-around;
  align-items:center;
}
.left-cont{
  width:40%;
  height:60%;
  font-size:2rem;
  position:relative;

}
.left-cont article{
  position:absolute;
  bottom:0;
}
.left-cont h2{
  margin-bottom:15px;
}
.left-cont p{
  line-height:200%;
}
.right-cont{   
  margin-top:40px; 
  width:40%;
}


/*------font-weight--------*/
.font-weight{
  height:100vh;
  display:flex;
  justify-content: space-between;
  align-items:center;
}
.font-weight article{
  margin-bottom:45px;
}
.left-cont2 p,.right-cont2 p{
  font-size:4.7rem;
}
.left-cont2{
  width:45%;
  text-align:center;
  font-weight:normal;
  font-size:5rem;
  margin-left:-150px;
}
.type{
  width:89px;
  display:inline-block;
  background:#fff;
  height:37px;
  line-height:37px;
  margin-bottom:20px;
  border-radius:25px;
  font-size:1.7rem;
  font-weight:bold;
}
.title{
  font-size:1.8rem;
  display:inline-block;
  margin-bottom:10px;
  font-weight:normal;
}
.right-cont2{
  width:45%;
  text-align:center;
  font-weight:bold;
  font-size:5rem;
  margin-right:-150px;
}

/*---emoticon---*/
.emoticon{
  padding-bottom:50px;
}
.emoticon p{
  font-size:2rem;
}
.emoticon > h1,
.emoticon > p, 
.e-cont h2{
  color:white;
}
.emoticon > h1,
.emoticon > p
{   
  margin-top:10px;
  text-align:center;
}
.emoticon > h1{
  padding-top:150px;
}
.e-cont h2{
  margin-bottom:10px;
}
.e-cont p{
  color:#818181;
  margin-top:5px;
}
.cont-box{
  display:flex;
  justify-content: space-between;
  align-items:center;
  margin:150px 0;
}
.svg{
  position:relative;
}
.e-img{
  width:280px;
  fill:#5b5b5b;
}
.yellow{
  position:absolute;
  left:0;
  bottom:0;
}
.yellowCenter{
  position:absolute;
  left:0;
}
.e1{
  width:320px;
}
.e2{
  width:140px;
}
.cls-1 {
  fill: #5b5b5b; 
  opacity: 0.54;
}
.cls-2 {
  fill: #FFCD00;
}

/*----example----*/
.example{
  width:100%;
  height:100vh;
  margin:auto;
  text-align:center;
}
.e-text{
  height:50%;
  margin:30% auto 0;
  background:none;
  border:none;
  font-size:100px;
  width:100%;
  text-align:center;
  resize: none;
  font-weight: bold;
}
.e-text:focus {
  outline: none;
}
.exam-botm{
  width:100%;
  display:flex;
  align-items: center
}
.control{
  width:150px;
  height:10px;
  position:relative;
}
.control input[type="range"]{
  position:absolute;
  top: 4px;
  height: 2px;
  width: 140px;
  background:#000;
	-webkit-appearance: none;
}
.control input[name="cBar3"]{
  position:absolute;
  top: 0px;
  height: 9px;
  width: 150px;
  background:linear-gradient(90deg, #FF3333,#F0FF33, #4CFF33, #33FFDC,#335AFF,#FF33FA,#FF3333);
	-webkit-appearance: none;
}
::-webkit-slider-thumb {
  height: 20px;
  width: 20px;
  border-radius: 50%;
  border: 1px solid #000;
  background-color: white;
  -webkit-appearance: none;
}

.exam-botm > li{
  margin-right:25px;
  text-align:left;
  font-size:23px;
  display:flex;
  align-items: center;
}
.exam-botm h3{
  font-size:2.5rem;
  margin-right:15px;
  font-weight: normal;
}
.exam-botm li span{
  margin-left:15px;
}
.exam-btn{
  display:flex;
  width:130px;
  justify-content: space-between;
}
.align-btn{
  position:relative;
}
.align{
  cursor: pointer;
  border:none;
  width:35px;
  height:35px;
  background:none;
}
.align-l,.align-r{display:none}
.mono{
  cursor: pointer;
  border:none;
  width:35px;
  height:35px;
  background:url(./img/black-btn.png)no-repeat center;
  background-size:100%;
}
.reset{
  cursor: pointer;
  border:none;
  width:32px;
  height:32px;
  background:none;
}

/*---actual-use---*/
.actual-use{
  height:100vh;
  display:flex;
  align-items:center;
}
.a-wrap{
  width:100%;
  height:60vh;
  display:flex;
  flex-wrap: wrap;
  align-items:center;
  justify-content: space-between;
}
.a-box{
  cursor: pointer;
  margin-bottom:40px;
  width:32%;
  height:33vh;
  border-radius:10px;
  box-shadow:3px 3px 10px #5f5f5f;
}
.a-box1{
  background:url(img/ex/1.PNG)no-repeat left;
  background-size:cover;
}
.a-box2{
  background:url(img/ex/2.PNG)no-repeat left;
  background-size:cover;
}
.a-box3{
  background:url(img/ex/3.jpg)no-repeat center;
  background-size:cover;
}
.a-box4{
  background:url(img/ex/4.png)no-repeat center;
  background-size:cover;
}
.a-box5{
  background:url(img/ex/5.PNG)no-repeat left;
  background-size:cover;
}
.a-box6{
  background:url(img/ex/6.jpg)no-repeat center;
  background-size:cover;
}

/*-----modal------*/
body.active-modal {
  overflow-y: hidden;
}
.modal{
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  position: fixed;
}

.overlay {
  background: rgba(49,49,49,0.8);
}
.m-btn{
  background:none;
  position: absolute;
  top:120px;
  right:200px;
  width:40px;
  height:40px;
  line-height:30px;
  border:none;
  outline: none;
  z-index: 9999;
  cursor:pointer;
  color:white;
}
.m-btn i{font-size:4rem;}

.s-img1{
  background:url(./img/ex/1.PNG)no-repeat center;
  background-size:100%;
  width:60%;
  height:600px;
}
.s-img2{
  background:url(./img/ex/2.PNG)no-repeat center;
  background-size:100%;
  width:60%;
  height:600px;
}
.s-img3{
  background:url(./img/ex/3.jpg)no-repeat center;
  background-size:100%;
  width:60%;
  height:600px;
}
.s-img4{
  background:url(./img/ex/4.png)no-repeat center;
  background-size:100%;
  width:60%;
  height:600px;
}
.s-img5{
  background:url(./img/ex/5.PNG)no-repeat center;
  background-size:100%;
  width:60%;
  height:600px;
}
.s-img6{
  background:url(./img/ex/6.jpg)no-repeat center;
  background-size:100%;
  width:100%;
  height:600px;
}


/*---download---*/
.download{
  height:100vh;
  position:relative;
  display:flex;
  align-items:center;
}
.f-wrap{
  display:flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.f-box{
  margin-bottom:60px;
  width:45%;
}
.f-box h1{
  margin-bottom:10px;
}
.f-box p{
  line-height:150%;
}
.subscr{
  cursor:pointer;
  font-size:2rem;
  width:300px;
  height:60px;
  padding:10px;
  background:#333;
  color:white;
  border:none;
  border-radius:12px;
  box-shadow: none;
  display:flex;
  align-items:center;
  justify-content: space-around;
}
.footer{
  width:100%;
  position:absolute;
  bottom:0px;
  display:flex;
  align-items:center;
  justify-content: space-between;
  font-size:20px;
}
.footer article img{
  width:150px;
  margin-left:20px;
  margin-bottom:-10px;
}
